.FormBody{
    min-height: 100vh;
    width: 100%;
    display: flex;
    gap:1rem;
    flex-direction: column;
    justify-content:center;
}

.FormBody form{
    width: 60%;
    padding:1rem;
    border-radius: 10px;
    background-color:var(--white);
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    gap:1rem;
    box-shadow: 0px 10px 2px 0 var(--blackO);
    
}

.OfferCard{
    padding:1rem;
    background-color:var(--Blue);
    border-radius: 10px;
    color: var(--Grayish-Blue);
    text-align: center;
    width: 60%;
    font-size: 0.8rem;
    box-shadow: 0px 7px 2px 0 var(--blackO);
}
.OfferCard span{
    color:var(--white);
    font-weight: 700;
    
}

.btn_submit{
    padding:1rem;
    cursor: pointer;
    font-weight: 700;
    color: var(--white);
    background-color: var(--Green);
    outline:none;
    border-radius: 5px;
    border:0;
    box-shadow: 0px -3px 2px 0px inset var(--blackO);
}

.Terms{
    font-size: 0.66rem;
    color: var(--Grayish-Blue);
}
.Terms span{
    color: var(--Red);
    font-weight: 700;
}

@media screen and (max-width:375px) {
    .FormBody{
        align-items: center;
        justify-content: start;
        min-height: auto;
        margin-bottom: 1.5rem;
    }
    .FormBody form{width: 80%;}
    .OfferCard{width: 80%;}
}