.Input{
    width: 100%;
    position:relative;
    display:flex;
    flex-direction: column;
}
.Input input{
    height:2rem;
    border-radius: 5px;
    padding:0.3rem 1rem;
}
.Input input[data-error="true"]{
    box-shadow: 0 0 1px 1px var(--Red);
}
.Input input[data-error="true"]::placeholder{
    color:var(--Red);
}
.Input input:focus-within{
    box-shadow: 0 0 2px 1px var(--black1);;
}
.Input p{
    display:none;
    text-align:right;
    font-size: 0.7rem;
    margin-top: 0.5rem;
    color: var(--Red);
}
.Input p[data-error="true"]{
    display: block;
}
.Input img{
    display: none;
    position:absolute;
    right:0;
    transform:translate(-40%,35%);
}
.Input img[data-error="true"]{
    display: block;
}