.Header{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Header div{
    width: 75%;
}
.Header div h1{
    font-size: 3rem;
    font-weight: 700;
    line-height: 3.5rem;
    color:var(--white);
    margin-bottom: 2rem;
}
.Header div p{
    text-align:justify;
    color:var(--white);
    opacity: 0.85;
}
@media screen and (max-width:375px) {
    .Header{min-height: 60vh;}
    .Header div{
        width: 90%;
        text-align: center;
    }
}