:root{
  /* Fonts */
  --Poppins:"Poppins";
  /* colors */
  --white:#ffffff;
  --black:#000000;
  --blackO:#0000001f;
  --black1:#00000098;

  /* ### Neutral */
  --Dark-Blue: hsl(249, 10%, 26%) ;
  --Grayish-Blue: hsl(246, 25%, 77%);
  /* ### Primary */

--Red: hsl(0, 100%, 74%) ;
--Green: hsl(154, 59%, 51%);

/* ### Accent */

--Blue: hsl(248, 32%, 49%);

}

*{
  font-family: var(--Poppins);
  margin:0px;
  padding:0px;
}

a{
  color:var(--black);
  text-decoration: none;
}
input{
  outline: none;
  border:0px;
  box-shadow: 0 0 1px 1px var(--Grayish-Blue);
}
input:focus-within{
  outline:none;
}